:root {
  --col-red: #9d3232;
  --col-light: #eae1d3;
}

*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

@media only screen and (max-width: 25em) {
  html {
    font-size: 50%;
  }
}

body {
  min-height: 100vh;
  color: var(--col-red);
  width: 100%;
  background: linear-gradient(45deg, #9d3232, #ba3b3b);
  place-items: center;
  padding: 3rem 3rem 3rem 1rem;
  font-family: Macondo, cursive;
  font-size: 2.5rem;
  font-weight: 400;
  display: grid;
  overflow-x: hidden;
}

body .author {
  color: var(--col-light);
  padding: 2rem;
  font-size: 2rem;
  position: absolute;
  top: 0;
  right: 0;
}

body .author a:link, body .author a:visited {
  color: inherit;
  text-decoration: none;
}

.content {
  max-width: 70rem;
  width: 100%;
  background: var(--col-light);
  grid-template-columns: 10% 1fr;
  display: grid;
  box-shadow: 16px 16px #beb6ab;
}

.content .aside-info {
  writing-mode: vertical-lr;
  background: var(--col-light);
  border-left: 1px solid var(--col-red);
  white-space: nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  padding: 4rem 0;
  display: flex;
  transform: rotate(180deg);
}

.content .aside-info .separator {
  width: 1px;
  height: 100%;
  background-color: var(--col-red);
}

.content .main-info {
  grid-template-rows: minmax(min-content, 34rem) minmax(min-content, 7rem);
  display: grid;
}

.content .main-info__game-field {
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
  padding: 2rem;
  display: flex;
}

.content .main-info__game-field .game-header {
  text-align: center;
  font-size: 4rem;
}

.content .main-info__game-field .game-word .word {
  flex-wrap: wrap;
  justify-content: center;
  gap: .2rem;
  display: flex;
}

.content .main-info__game-field .game-word .word .letter {
  height: 3.5rem;
  width: 3.5rem;
  border: 2px solid var(--col-red);
  background: #c92d1f1a;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.content .main-info__game-field .game-word .word .letter.guessed {
  background: none;
}

.content .main-info__game-field .game-info {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.content .main-info__button-field {
  border-top: 1px solid var(--col-red);
  justify-content: center;
  align-items: center;
  display: flex;
}

.message {
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(15px);
  backdrop-filter: blur(15px);
  z-index: 5;
  background: #eae1d380;
  font-size: 3rem;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.message.show {
  justify-content: center;
  align-items: center;
  display: flex;
}

.message--win, .message--lose {
  text-align: center;
  display: none;
}

.message--win .mg-s, .message--lose .mg-s {
  margin-bottom: 4rem;
}

.message--win.show, .message--lose.show {
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.message .correct-spell {
  font-size: 2.7rem;
  text-decoration: underline;
}

.btn {
  height: 100%;
  width: 100%;
  font: inherit;
  color: inherit;
  cursor: pointer;
  background: none;
  border: none;
  position: relative;
}

.btn span {
  transition: all .2s;
}

.btn:hover span {
  margin-right: 1rem;
}

.active {
  background: var(--col-red);
  color: var(--col-light);
}

